import React, {Component} from 'react';

import GeneralNav from './nav'

class CmdButton extends Component {
	render () {
        return (
            <button 
            className="quickcommand" 
            onClick={this.SendMessage}>
                <p>{this.props.text}</p>
               

            <style>{`
            .quickcommand{
				padding: 5px 10px;
				text-align: center;
				background-color: #131418;
				font-size: 14px;
				margin-top: 3px;
                cursor: pointer;
                width: 110px;
                color: white;
                margin: 5px;
                border: none;
            }
            `}</style>
		</button>
        )
    };

    SendMessage = () => {
        var action = Object.keys(this.props.message)[0] //hacky to keep the props passing short. it wouldve been nasty to specify the entire object 
        global.ipcRenderer.send('message', {action: action, data: this.props.message[action]})
    }
}

class NumValCommand extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 16
        };
    }

	render () {
        return (
        <div>
            <p>{this.props.text}</p>
            <select onChange={this.HandleChange}>
                <option>16</option>
                <option>18</option>
                <option>20</option>
                <option>22</option>
                <option>24</option>
                <option>26</option>
                <option>28</option>
                <option>30</option>
                <option>40</option>
                <option>60</option>
                <option>80</option>
                <option>100</option>
                <option>120</option>
                <option>140</option>
                <option>160</option>
                <option>200</option>
                <option>300</option>
                <option>400</option>
                <option>500</option>
            </select>
            <button onClick={this.Execute}>Go</button>
        </div>
        )
    };

    HandleChange = (e) => {
        var newValue = e.target.value
        this.setState({value: newValue})
    }

    Execute = () => {
        global.ipcRenderer.send('message', {action: 'clicommand', data: this.props.command + " me " + this.state.value})
    }
}

class MagnetizeTo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: ""
        };
    }

	render () {
        return (
        <div>
            <p>Magnetize to</p>
            <input type="text" placeholder="Exact username" onChange={this.HandleChange}/>
            <button onClick={this.Execute}>Go</button>
        </div>
        )
    };

    HandleChange = (e) => {
        this.setState({username: e.target.value})
    }

    Execute = () => {
        global.ipcRenderer.send('message', {action: "luascript", script: `_G.MagnetizeToTarget = "${this.state.username}"`})
        global.ipcRenderer.send('message', {action: 'storedluascript', data: {filename:'general/magnetizeto.lua'}})
    }
}

class TeleportVector3 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            x: 0,
            y: 0,
            z: 0
        };
    }

	render () {
        return (
        <div id="tpv3com">
            <p>Teleport To Vector3(X,Y,Z)</p>
            <input type="number" placeholder="x" defaultValue="0" onChange={this.HandleXChange}/>
            <input type="number" placeholder="y" defaultValue="0" onChange={this.HandleYChange}/>
            <input type="number" placeholder="z" defaultValue="0" onChange={this.HandleZChange}/>
            <button onClick={this.Execute}>Go</button>
        </div>
        )
    };

    HandleXChange = (e) => {
        this.setState({x: e.target.value})
    }

    HandleYChange = (e) => {
        this.setState({y: e.target.value})
    }

    HandleZChange = (e) => {
        this.setState({z: e.target.value})
    }

    Execute = () => {
        global.ipcRenderer.send('message', {action: 'clicommand', data: `vectorteleport ${this.state.x} ${this.state.y} ${this.state.z}`})
    }
}

class Buttons extends Component {
	render () {
        return (
		<>
            <GeneralNav/>
            <div className="section">
                <h2>Common</h2>
                <div className="quickcommands">
                    <CmdButton text="ctrl+click tp" message={{"clicommand": "toggleclickteleport"}}/>
                    <CmdButton text="Infinite Jump" message={{"storedluascript": {filename:'general/infinitejump.lua'}}}/>
                    <button className="quickcommand" onClick={this.scrollToESP}>ESP</button>
                    <button className="quickcommand" onClick={this.scrollToAimbot}>Aimbot</button>
                    <CmdButton text="Fly" message={{"storedluascript": {filename:'general/fly.lua'}}}/>
                    <CmdButton text="God mode" message={{"storedluascript": {filename:'general/god.lua'}}}/>
                    <CmdButton text="Noclip" message={{"storedluascript": {filename:'general/noclip.lua'}}}/>
                    <CmdButton text="Flashlight" message={{"clicommand": "flashlight me"}}/>
                    <CmdButton text="No Legs" message={{"clicommand": "nolegs me"}}/>
                    <CmdButton text="Float" message={{"clicommand": "float me"}}/>
                    <CmdButton text="Chat troll" message={{"storedluascript": {filename:'general/chattroll.lua'}}}/>
                    <CmdButton text="High Hips" message={{"clicommand": "hipheight me 10"}}/>
                    <CmdButton text="Btools" message={{"clicommand": "btools me"}}/>
                    <CmdButton text="Morning" message={{"clicommand": "morning"}}/>
                    <CmdButton text="Night" message={{"clicommand": "night"}}/>
                    <CmdButton text="Suicide" message={{"clicommand": "kill me"}}/>
                    <CmdButton text="Limp" message={{"clicommand": "limp me"}}/>
                    <CmdButton text="Blockhead" message={{"clicommand": "blockhead me"}}/>
                </div>
            </div>
            <br/>
            <div id="specgencoms" className="section">
                <NumValCommand text="Walk Speed" command="speed"/>
                <NumValCommand text="Jump Power" command="jumppower"/>
                <MagnetizeTo/>
                <TeleportVector3/>
            </div>
            <br/>
            <div ref="esp" className="section">
                <h2>ESP Toggles</h2>
                <div className="quickcommands">
                    <CmdButton text="ESP" message={{"clicommand": "boxesp"}}/>
                    <CmdButton text="Team Check" message={{"clicommand": "boxespteamcheck"}}/>
                    <CmdButton text="Lines" message={{"clicommand": "boxesplines"}}/>
                    <CmdButton text="Names" message={{"clicommand": "boxespnames"}}/>
                    <CmdButton text="Distance" message={{"clicommand": "boxespdistance"}}/>
                </div>
            </div>
            <br/>
            <div ref="aimbot" className="section">
                <h2>Aimbot Toggles</h2>
                <div className="quickcommands">
                    <CmdButton text="Aimbot" message={{"storedluascript": {filename: "general/aimbot.lua"}}}/>
                    <CmdButton text="Team Check" message={{"luascript": "_G.WRDAimbotTeamCheck = not _G.WRDAimbotTeamCheck"}}/>
                    <CmdButton text="Wallcheck" message={{"luascript": "_G.WRDAimBotWallcheck = not _G.WRDAimBotWallcheck"}}/>
                    <CmdButton text="Bypass" message={{"luascript": "_G.WRDAimbotBypass = not _G.WRDAimbotBypass"}}/>
                </div>
            </div>
            <br/>
            <div className="section">
                <h2>Client Appearance</h2>
                <div className="quickcommands">
                    <CmdButton text="Fire" message={{"clicommand": "fire me"}}/>
                    <CmdButton text="Highlight" message={{"clicommand": "highlight me"}}/>
                    <CmdButton text="Smoke" message={{"clicommand": "smoke me"}}/>
                    <CmdButton text="Sparkles" message={{"clicommand": "sparkles me"}}/>
                    <CmdButton text="No Arms" message={{"clicommand": "noarms me"}}/>
                    <CmdButton text="Invisible" message={{"clicommand": "invisible me"}}/>
                    <CmdButton text="Forcefield" message={{"clicommand": "ff me"}}/>
                    <CmdButton text="4D Character" message={{"storedluascript": {filename: "general/multidimensionalcharacter.lua"}}}/>
                </div>
            </div>
            <br/>
            <div className="section">
                <h2>Animations</h2>
                <div className="quickcommands">
                    <CmdButton text="Dab" message={{"storedluascript": {filename: "animations/dab.lua"}}}/>
                    <CmdButton text="Jump Land" message={{"storedluascript": {filename: "animations/jumpland.lua"}}}/>
                    <CmdButton text="Levitate" message={{"storedluascript": {filename: "animations/levitate.lua"}}}/>
                    <CmdButton text="Walkthrough" message={{"storedluascript": {filename: "animations/walkthrough.lua"}}}/>
                    <CmdButton text="Energize GUI" message={{"storedluascript": {filename: "animations/energizegui.lua"}}}/>
                </div>
            </div>
            <br/>
            <div className="section">
                <h2>Etc</h2>
                <div className="quickcommands">
                    <CmdButton text="TP Tool" message={{"storedluascript": {filename: "general/tptool.lua"}}}/>
                </div>
            </div>

            <style>{`
            .section{
                padding: 12px;
            }

            .quickcommands{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            #specgencoms{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            #specgencoms > div{
                margin: 25px;
                width: 230px;
            }

            #specgencoms > div > p:nth-child(1){
                color: gray;
                margin-bottom: 5px;
            }

            #specgencoms input, #specgencoms select{
                border: 1px solid #ccc;
                padding: 8px;
                width: 100%;
                max-width: 180px;
                margin-right: 5px;
            }

            #specgencoms button{
                border: 1px solid #ccc;
                padding: 8px;
            }

            #tpv3com input{
                width: 57px;
                display: inline-block;
                margin-right: 5px;
            }

            h2{
                text-align: center;
            }
            `}</style>
		</>
        )
    };

    scrollToESP = () => {
        var target = this.refs.esp
        target.parentNode.scrollTop = target.offsetTop;
    }

    scrollToAimbot = () => {
        var target = this.refs.aimbot
        target.parentNode.scrollTop = target.offsetTop;
    }
}

export default Buttons