import React, {Component} from 'react';
import {connect} from 'react-redux'

class Header extends Component {
	render () {
        var styles = {
            topMost: {
                backgroundColor: this.props.topMost ? "#27ae60" : "red"
            }
        }

        return (
		<nav>
            <h1>JJSploit</h1>
            <div id="actions">
                <button id="fixroblox" onClick={this.FixRoblox}>Fix Roblox</button>
                <button id="alwaysontop" style={styles.topMost} onClick={this.ToggleTopMost}>Top Most</button>
                <button id="exit" onClick={this.ExitApp}>X</button>
            </div>
            <style>{`
            nav{
                background-color: #131418;
                display: flex;
                color: white;
                align-items: center;
                z-index: 2;
                -webkit-app-region: drag
            }

            h1{
                flex: 1;
                font-weight: 400;
                margin: 0;
                padding: 4px;
                color: white;
                font-size: 14px;
                font-weight: 300;
            }

            #actions{
                display: flex;
            }

            #actions button{
                border: 0;
                margin-left: 2px;
                background-color: rgba(255,255,255,0.1);
                -webkit-app-region: no-drag;
            }

            #fixroblox{
                cursor: pointer;
                border: 0;
                padding: 4px;
                color: gray;
                font-weight: 500;
            }

            #fixroblox:hover{
                color: white;
                background-color: rgba(255,255,255,0.2);
            }

            #actions #alwaysontop{
                cursor: pointer;
                border: 0;
                padding: 4px;
                background-color: #27ae60;
                color: white;
                font-weight: 500;
            }

            #exit{
                background-color: transparent;
                color: gray;
                cursor: pointer;
                width: 30px;
                height: 30px;
                text-align: center;
                line-height: 200%;
            }

            #exit:hover{
                background-color: red;
                color: white;
            }
            `}</style>
		</nav>
        )
    };
    
    ToggleTopMost = () => {
        global.ipcRenderer.send('message', {action:"toggletopwindow"})
    }

    FixRoblox = () => {
        global.ipcRenderer.send('message', {action:"fixroblox"})
    }

    ExitApp = () => {
        global.ipcRenderer.send('message', {action:"exitapp"})
    }
}

function mapStateToProps (state) {
    const { topMost } = state.Settings
    return { topMost }
}

export default connect(mapStateToProps)(Header)