import React from 'react';

function App() {
	return (
		<footer>
            <p id="version">v5.3.4</p>
            <a id="site" href="https://wearedevs.net/" target="_blank" rel="noopener noreferrer">wearedevs.net</a>

            <style>{`
            footer{
                position: relative;
                background-color: #131418;
                color: white;
                font-weight: 300;
                padding: 4px;
            }

            #site{
                display: block;
                text-align: center;
                text-decoration: none;
                font-weight: 300;
                color: white;
                font-size: 14px;
            }
            
            #version{
                position: absolute;
                right: 0;
                font-size: 14px;
                margin-top: 1px;
                margin-right: 4px;
            }
            `}</style>
		</footer>
	);
}

export default App;
