import React, {Component} from 'react';
import {connect} from 'react-redux'
import axios from 'axios';

import {SET_MESSAGE, SET_FORM_VIEW} from '../../redux/actions'

class KeyAccess extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ip: "",
            ipHidden: true
        };
    }

	render () {
        return (
		<div id="keyaccess">
            <p>You must unlock your IP first. Please go to the following link and continue as requested. Click the Verify Unlock button when you have finished!</p>
            <br/>
            <a href="https://mboost.me/ae" target="_blank" rel="noopener noreferrer">https://mboost.me/ae</a>
            <br/>
            <button onClick={this.UnlockApp} className="unlockbtn">Verify Unlock</button>
            <br/>
            <p>
                Your IP:
                <span style={{color: "gray"}}> {this.state.ipHidden ? "hidden.x.x.x" : this.state.ip} </span>
                <button onClick={this.ToggleIpVisible} className="showipbtn">{this.state.ipHidden ? "Show IP" : "Hide IP"}</button>
            </p>
            

            <style>{`
            #keyaccess{
                padding: 12px;
                height: 100%;
            }

            #keyaccess a{
                text-align: center;
                display: block;
            }

            .unlockbtn{
                background-color: #4CAF50;
                border: none;
                color: white;
                padding: 12px 32px;
                text-align: center;
                text-decoration: none;
                display: block;
                font-size: 16px;
                margin: auto;
                border-radius: 3px;
            }

            .showipbtn{
                border: none;
                padding: 5px 10px;
                border-radius: 3px;
            }
            `}</style>
		</div>
        )
    };

    ToggleIpVisible = () => {
        if(!this.state.ip){
            var {dispatch} = this.props
            dispatch(SET_MESSAGE({subject: "rejection", text: "Try unlocking JJsploit before using the Show IP button"}))
        } 
        else{
            this.setState({ipHidden: !this.state.ipHidden})
        }
    }

    UnlockApp = () => {
        var {dispatch} = this.props
        axios.post("https://api.mboost.me/v1/keyaccess/usekey?pageid=Gb", {withIP: true})
        .then((res) => {
            this.setState({ip: res.data.ip})

            if(res.data.success === true){
                //Let the main process know the client has unlocked the app
                global.ipcRenderer.send('message', {action:"unlockedApp"})
                //Move to general exploits page
                dispatch(SET_FORM_VIEW("General"))
            } else{
                dispatch(SET_MESSAGE({subject: "error", text: "Unlock failed: Make sure that your IP address from JJSploit matches the IP on the unlocker page. Otherwise go back to the page and unlock again!"}))
            }
        })
        .catch(function (error) {
            dispatch(SET_MESSAGE({subject: "error", text: "There was an internet connection issue when contacting the mboost.me server. Make sure a firewall is not blocking access."}))
        });
    }
}

function mapStateToProps (state) {
    const { topMost } = state.Settings
    return { topMost }
}

export default connect(mapStateToProps)(KeyAccess)