import React, {Component} from 'react';
import {connect} from 'react-redux'

import YTLogo from '../../assets/50x50.png'
import DiscordHeadWhite from '../../assets/discord-head-white.png'
import DomainIcon from '../../assets/domain.png'

class PromoButton extends Component {
    constructor(props){
        super(props)
        this.state = {
            icon: "#"
        }
    }

    componentWillMount(){
        switch(this.props.type){
            case "YouTube":
                this.setState({icon: YTLogo, bgColor: "#fe4c40"})
                break
            case "Discord":
                this.setState({icon: DiscordHeadWhite, bgColor: "#7289da"})
                break;
            case "Website":
                this.setState({icon: DomainIcon, bgColor: "#3498db"})
                break;
            default: break;
        }
    }

	render () {
        return (
            <>
                <a className="ytbutton" style={{backgroundColor: this.state.bgColor}} href={this.props.link} target="_blank" rel="noopener noreferrer">
                    <img src={this.state.icon} alt=""/>
                    <p>{this.props.text}</p>
                </a>
                <style>{`
                .ytbutton{
                    max-width: 180px;
                    width: 100%;
                    height: 45px;
                    background-color: #fe4c40;
                    border-radius: 4px;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    margin: 8px 6px;
                    color: white;
                    border-radius: 4px;
                    text-decoration: none;
                    display: flex;
                    align-items: center;
                    font-size: 14px;
                    padding: 6px 8px;
                }

                .ytbutton img{
                    margin-right: 5px;
                    height: 25px;
                    width: 25px;
                    margin-right: 8px;
                }
                `}</style>
            </>
        )
    };

    Attach = () => {
        global.ipcRenderer.send('message', {action:"attach"})
    }
}

class Attach extends Component {
	render () {
        return (
		<>
            <div id="split1" className="section">
                <div id="attachcontainer">
                    <div>
                        <h1>You must attach JJSploit to a game first</h1>
                    </div>
                    <div className="attachMethods">
                        <button className="btnAttach" onClick={this.Attach}>Attach {this.props.betaModuleFound && "Classic"}</button>
                        {this.props.betaModuleFound && <button className="btnAttach" onClick={this.AttachBeta} title="More stable and powerful in some aspects, but does not work for everyone.">Attach Beta</button>}
                    </div>
                    <p id="ServerMessage">{this.props.serverMessage || "Loading server message..."}</p>
                </div>
                <div id="ytbuttons">
                    <PromoButton link="https://www.youtube.com/WeAreDevsYT" text="Learn To Code" type="YouTube"/>
                    <PromoButton link="https://wearedevs.com" text="Blog" type="Website"/>
                    <PromoButton link="https://wearedevs.net/discord" text="Discord" type="Discord"/>
                    <PromoButton link="https://www.youtube.com/channel/UC3YNONzSHPW12m3AT48fMHw" text="Exploit Tutorials" type="YouTube"/>
                </div>
            </div>
            <br/>
            <div className="section">
                <p>Notes:</p>
                <p>*If it says its patched, you must wait for the creators to update! This could be a few hours.</p>
                <p>*If its taking long for you to update, something may be blocking you from receiving the update. Make sure you disable any anti-virus or firewall.</p>
                <p>*You can ask for help at <a href="https://wearedevs.net/forum" target="_blank" rel="noopener noreferrer">https://wearedevs.net/forum</a></p>
            </div>
            <br/>
            <div id="split2" className="section">
                <p><b>Credits:</b></p>
                <ul>
                    <li>Powered by the <a href="https://wearedevs.net/d/Exploit%20API" target="_blank" rel="noopener noreferrer">WeAreDevs Exploit API</a></li>
                    <li>Imperious, ThunderMods, and Robomat for participation in the module development</li>
                    <li>Bditt for the ESP and misc.</li>
                    <li>Jazzy for huge script contribution</li>
                </ul>
            </div>

            <style>{`
            .section{
                padding: 12px;
            }

            #split1{
                height: 100%;
                display: flex;
                flex-direction: column;
            }

            #attachcontainer{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex: 1;
            }

            #split2 ul li{
                margin-left: 16px;
            }

            #split1 h1{
                color: black; 
                text-align: center; 
                font-size: 22px;
                margin-bottom: 10px;
                padding: 0;
            }

            .attachMethods{
                display: flex;
                justify-content: space-around;
                width: 100%;
                margin-bottom: 12px;
            }

            .btnAttach{
                max-width: 180px;
                width: 100%;
                height: 45px;
                margin: 0 12px;
                border: none;
                background: #4CAF50;
                color: white;
                cursor: pointer;
                border-radius: 4px;
                font-size: 14px;
                padding: 12px 0;
            }

            #ServerMessage{
                text-align: center; 
                color: gray; 
                width: 100%; 
                max-height: 70px;
                overflow: auto;
                margin-bottom: 12px;
            }

            #ytbuttons{
                display: flex;
                justify-content: space-around;
                width: 100%;
            }
            `}</style>
		</>
        )
    };

    Attach = () => {
        global.ipcRenderer.send('message', {action:"attach"})
    }

    AttachBeta = () => {
        global.ipcRenderer.send('message', {action:"attach-beta"})
    }
}

function mapStateToProps (state) {
    const { topMost, serverMessage, betaModuleFound } = state.Settings
    return { topMost, serverMessage, betaModuleFound }
}

export default connect(mapStateToProps)(Attach)