import React, {Component} from 'react';
import {connect} from 'react-redux'

import Footer from './footer';
import SideMenu from './sidemenu'
import FormHandler from './forms'

class ContentContainer extends Component {
    render(){
        var menuBlacklist = [undefined, "Attach", "KeyAccess", "Agreement"]
        var {form} = this.props

        return (
            <div id="container">
                <div id="content">
                    {/* Hide the menu from some forms */}
                    {menuBlacklist.indexOf(form)===-1 && <SideMenu/>}
                    <FormHandler/>
                </div>
                <Footer/>
                
                <style>{`
                #container{
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                }

                #content{
                    flex: 1;
                    overflow-y: scroll;
                    overflow-x: hidden;
                    position: relative;
                }
                `}</style>
            </div>
        );
    }
}

function mapStateToProps (state) {
    const { form } = state.FormHandler
    return { form }
}

export default connect(mapStateToProps)(ContentContainer)